import React from "react";
import { Container, Flex, Box, Text, Link } from "rebass";

const Footer = props => {
  console.log(props.context);
  return (
    <>
      <Flex
        justifyContent="space-around"
        fontSize={[1, 2]}
        backgroundColor="whites.5"
        my={[3, 4]}
      >
        <Box
          maxWidth={[768, null, 1024, null, "100%"]}
          {...props}
          sx={{
            display: "grid",
            gridTemplateRows: ["repeat(4, 32px)", "repeat(4, 50px)"],
            gridTemplateColumns: ["min-content", "repeat(2, 1fr)"],
            gridAutoFlow: "column",
            px: 0,
            py: 0
          }}
        >
          <Link to="/" sx={{ variant: "styles.navlink", p: 2 }}>
            Home
          </Link>
          <Link to="/work" sx={{ variant: "styles.navlink", p: 2 }}>
            Work
          </Link>
          <Link to="/blog" sx={{ variant: "styles.navlink", p: 2 }}>
            Blog
          </Link>
          <Link to="/about" sx={{ variant: "styles.navlink", p: 2 }}>
            About
          </Link>
          <Link to="/products" sx={{ variant: "styles.navlink", p: 2 }}>
            Products
          </Link>
          <Link to="/community" sx={{ variant: "styles.navlink", p: 2 }}>
            Community
          </Link>
          <Link to="/support" sx={{ variant: "styles.navlink", p: 2 }}>
            Support
          </Link>
          <Link to="/contact" sx={{ variant: "styles.navlink", p: 2 }}>
            Contact
          </Link>
          <Link to="/support" sx={{ variant: "styles.navlink", p: 2 }}>
            Support
          </Link>
          <Link to="/products" sx={{ variant: "styles.navlink", p: 2 }}>
            Products
          </Link>
          <Link to="/contact" sx={{ variant: "styles.navlink", p: 2 }}>
            Contact
          </Link>
          <Link to="/community" sx={{ variant: "styles.navlink", p: 2 }}>
            Community
          </Link>
          <Link to="/products" sx={{ variant: "styles.navlink", p: 2 }}>
            Products
          </Link>
          <Link to="/community" sx={{ variant: "styles.navlink", p: 2 }}>
            Community
          </Link>
          <Link to="/support" sx={{ variant: "styles.navlink", p: 2 }}>
            Support
          </Link>
          <Link to="/contact" sx={{ variant: "styles.navlink", p: 2 }}>
            Contact
          </Link>
        </Box>
      </Flex>
      {/* </Box> */}
    </>
  );
};
export default Footer;
